.listButton {
    width: 90px;
    float: right;
    text-transform: uppercase;
    text-align: center;
    font-weight: 700;
    letter-spacing: 0.02em;
}

.listBorder {
    border: 1px solid #066a89;
    /* margin: 20px 0; */
    -webkit-box-shadow: 0 0 10px #ddd;
            box-shadow: 0 0 10px #ddd;
}

.listColor {
    background-color: #eee;
    color: #066a89;
    margin: 15px;
}

.listHeader {
    text-align: center;
    text-transform: uppercase;
    color: #64cbe6;
    margin-top: 30px;
    padding: 10px;
}



